/** ìµœì†Œê°’ (ìˆ«ìž) */
export const min = (minValue: number, message?: string): ValidationRule => ({
  validate: (value) => {
    if (!value) return true;
    const num = parseFloat(value);
    return !isNaN(num) && num >= minValue;
  },
  message: message || `${minValue} ì´ìƒì´ì–´ì•¼ í•©ë‹ˆë‹¤`,
});

/** ìµœëŒ€ê°’ (ìˆ«ìž) */
export const max = (maxValue: number, message?: string): ValidationRule => ({
  validate: (value) => {
    if (!value) return true;
    const num = parseFloat(value);
    return !isNaN(num) && num <= maxValue;
  },
  message: message || `${maxValue} ì´í•˜ì—¬ì•¼ í•©ë‹ˆë‹¤`,
});

/** ë¹„ë°€ë²ˆí˜¸ í˜•ì‹ (ì˜ë¬¸, ìˆ«ìž, íŠ¹ìˆ˜ë¬¸ìž ì¡°í•©) */
export const password = (message = "ì˜ë¬¸, ìˆ«ìž, íŠ¹ìˆ˜ë¬¸ìžë¥¼ í¬í•¨í•´ì•¼ í•©ë‹ˆë‹¤"): ValidationRule => ({
  validate: (value) => {
    if (!value) return true;
    const hasLetter = /[a-zA-Z]/.test(value);
    const hasNumber = /\d/.test(value);
    const hasSpecial = /[!@#$%^&*(),.?":{}|<>]/.test(value);
    return hasLetter && hasNumber && hasSpecial;
  },
  message,
});

/** ë¹„ë°€ë²ˆí˜¸ í™•ì¸ (ë‹¤ë¥¸ í•„ë“œì™€ ì¼ì¹˜) */
export const matches = (fieldName: string, message = "ê°’ì´ ì¼ì¹˜í•˜ì§€ ì•ŠìŠµë‹ˆë‹¤"): ValidationRule => ({
  validate: (value, formValues) => {
    if (!value || !formValues) return true;
    return value === formValues[fieldName];
  },
  message,
});

/** íœ´ëŒ€í° ë²ˆí˜¸ (í•œêµ­) */
export const phone = (message = "ì˜¬ë°”ë¥¸ íœ´ëŒ€í° ë²ˆí˜¸ í˜•ì‹ì´ ì•„ë‹™ë‹ˆë‹¤"): ValidationRule => ({
  validate: (value) => {
    if (!value) return true;
    // 010-1234-5678, 01012345678 ë“± í—ˆìš©
    const cleaned = value.replace(/-/g, "");
    return /^01[016789]\d{7,8}$/.test(cleaned);
  },
  message,
});

/** ì»¤ìŠ¤í…€ ê²€ì¦ */
export const custom = <T = string>(
  validateFn: (value: T, formValues?: Record<string, unknown>) => boolean,
  message: string
): ValidationRule<T> => ({
  validate: validateFn,
  message,
});

// ê²€ì¦ ì‹¤í–‰ í•¨ìˆ˜

export function validate<T = string>(
  value: T,
  rules: ValidationRules<T>,
  formValues?: Record<string, unknown>
): string | null {
  for (const rule of rules) {
    if (!rule.validate(value, formValues)) {
      return rule.message;
    }
  }
  return null;
}

export function validateField(
  value: string,
  rules: ValidationRules,
  formValues?: Record<string, unknown>
): { isValid: boolean; error: string | null } {
  const error = validate(value, rules, formValues);
  return {
    isValid: error === null,
    error,
  };
}
